
import Loading from 'vux/src/components/loading/index.vue'

import {mapState} from 'vuex'

export default {
name: 'app',
components: {
Loading
},
computed: {
...mapState({
direction: state => state.app.direction,
isLoading: state => state.app.isLoading
})
}
}



